import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Header from '@/components/commons/Header.vue'   //
import Footer from '@/components/commons/Footer.vue'   //
import Banner from '@/components/commons/Banner.vue'   //
import RecommenedList from '@/components/commons/RecommenedList.vue'   //

import Home from '@/components/index/Home.vue'  //
import Category from '@/components/index/Category.vue'   //
import Rank from '@/components/index/Rank.vue'   //
import Comic from '@/components/index/Comic.vue'   //
import Chapter from '@/components/index/Chapter.vue'   //
import ComicList from '@/components/index/ComicList.vue'  //

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
	  name: 'Home',
      component: Home
    },
	{
	  path: '/home',
	  name: 'Home',
	  component: Home
	},
	{
	  path: '/category',
	  name: 'Category',
	  component: Category
	},
	{
	  path: '/rank',
	  name: 'Rank',
	  component: Rank
	},
	{
	  path: '/comic',
	  name: 'Comic',
	  component: Comic,	  
	},
	{
	  path: '/chapter',
	  name: 'Chapter',
	  component: Chapter
	},
	{
	  path: '/comiclist',
	  name: 'ComicList',
	  component: ComicList
	}
  ]
})



